import React from 'react';
import { ButtonContainer } from '../button-container/button-container';

type ButtonProps = React.ButtonHTMLAttributes<HTMLButtonElement>

export const PrimaryButton = ({ children, className, ...restProps }: ButtonProps) => (
  <ButtonContainer
    shouldHideOnClick={false}
    {...restProps}
    className={className}
    buttonClass="bg-main-purple"
  >
    {children}
  </ButtonContainer>
);
