import { SecondaryButton } from 'components/molecues/button';
import { Link, navigate } from 'gatsby';
import { useCart } from 'infrastructure/equipment/use-cart';
import React, { useEffect } from 'react';

interface SuccessPageContentProps {
  cartId: string,
}

export const SuccessPageContent = ({ cartId }: SuccessPageContentProps) => {
  const { cart, resetCart, isLoaded } = useCart();

  useEffect(() => {
    if (isLoaded) {
      if (cart.id !== cartId) {
        navigate('/');
      } else {
        resetCart();
      }
    }
  }, [isLoaded]);

  return (
    <div className="w-full items-center px-8 md:px-24 lg:px-32">
      <div className="flex h-full grow animate-fadeIn flex-col items-center justify-center gap-8">
        <div className="text-center">
          <h1 className="font-uni-sans text-3xl font-black">Sukces!</h1>
          <h2>
            Otrzymaliśmy Twoją wpłatę
          </h2>
        </div>
        <Link to="/"><SecondaryButton className="w-fit">Wróć na stronę główną</SecondaryButton></Link>
      </div>
    </div>
  );
};
