import React from 'react';
import { ButtonContainer } from '../button-container/button-container';

type ButtonProps = React.HTMLAttributes<HTMLButtonElement>

export const SecondaryButton = ({ children, className, ...restProps }: ButtonProps) => (
  <ButtonContainer
    {...restProps}
    className={className}
    buttonClass="border border-gradient bg-theme-gradient-background-dark hover:active:bg-theme-gradient-background-light"
  >
    {children}
  </ButtonContainer>
);
